<template>
  <section class="server_wrapper">
    <div ref='server_wrapper' class="server-content-wrapper">
    <h1>ITO</h1>
    <div class="server-content">
      <div class="list-title">
        <div class="current"></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
      <!---->
      <div style="float: left">
        <div id="circle1" class="circle mar" >
          <div id="circle11" class="circle-2">
          </div>
        </div>
        <div id="circle111" class="circle-1 circlehide mar">
          <div class="circle-2 circle-3">
          </div>
        </div>
        <div id="circleLine1" class="circle-Line"></div>

        <div id="circle2" class="circle marg circlehide">
          <div id="circle22" class="circle-2">
          </div>
        </div>
        <div id="circle222" class="circle-1  marg">
          <div class="circle-2 circle-3">
          </div>
        </div>
        <div  id="circleLine2" class="circle-Line-white"></div>

        <div id="circle3" class="circle circlehide marg" >
          <div id="circle33" class="circle-2">
          </div>
        </div>
        <div  id="circle333" class="circle-1  marg">
          <div class="circle-2 circle-3">
          </div>
        </div>
        <div  id="circleLine3" class="circle-Line-white"></div>

        <div id="circle4" class="circle circlehide marg" >
          <div id="circle44" class="circle-2">
          </div>
        </div>
        <div id="circle444" class="circle-1  marg">
          <div class="circle-2 circle-3">
          </div>
        </div>
      </div>

      <div class="list-content">
        <div ref="serverTitle" class="content-title">
          
        </div>
        <div ref="serverInfo" class="content">
          
        </div>
        <div class="moreDetail"></div>
      </div>
    </div>
  </div>
  </section>
</template>
<script type="text/javascript">

  export default {
    name: 'ServerContent',
    data() {
      return {
      }
    },
    mounted:function(){
      $(window).scroll(this.handleTouchMove);
      this.bindServer();
    },
    methods:{
      bindServer(e) {
        let server = $(".list-title div");
        var content = this;
        server.each(function(i) {
          $(this).on("click", function() {
            if($(this).index()==0){

              $("#circle1").addClass("circleshow").removeClass("circlehide");
              $("#circle111").addClass("circlehide").removeClass("circleshow");
              $("#circle2").addClass("circlehide").removeClass("circleshow");
              $("#circle222").addClass("circleshow").removeClass("circlehide");
              $("#circle3").addClass("circlehide").removeClass("circleshow");
              $("#circle333").addClass("circleshow").removeClass("circlehide");
              $("#circle4").addClass("circlehide").removeClass("circleshow");
              $("#circle444").addClass("circleshow").removeClass("circlehide");
              $("#circleLine2").addClass("circle-Line-white").removeClass("circle-Line");
              $("#circleLine1").addClass("circle-Line").removeClass("circle-Line-white");
              $("#circleLine3").addClass("circle-Line-white").removeClass("circle-Line");
              content.$refs.serverTitle.innerHTML='';
              content.$refs.serverInfo.innerHTML='';
            }else  if($(this).index()==1){
              $("#circle2").addClass("circleshow").removeClass("circlehide");
              $("#circle222").addClass("circlehide").removeClass("circleshow");
              $("#circle1").addClass("circlehide").removeClass("circleshow");
              $("#circle111").addClass("circleshow").removeClass("circlehide");
              $("#circle3").addClass("circlehide").removeClass("circleshow");
              $("#circle333").addClass("circleshow").removeClass("circlehide");
              $("#circle4").addClass("circlehide").removeClass("circleshow");
              $("#circle444").addClass("circleshow").removeClass("circlehide");
              $("#circleLine1").addClass("circle-Line-white").removeClass("circle-Line");
              $("#circleLine2").addClass("circle-Line").removeClass("circle-Line-white");
              $("#circleLine3").addClass("circle-Line-white").removeClass("circle-Line");

              content.$refs.serverTitle.innerHTML='';
              content.$refs.serverInfo.innerHTML='CMMIISO9001';
            }else  if($(this).index()==2){
              content.$refs.serverTitle.innerHTML='';
              content.$refs.serverInfo.innerHTML='';

              $("#circle3").addClass("circleshow").removeClass("circlehide");
              $("#circle333").addClass("circlehide").removeClass("circleshow");
              $("#circle1").addClass("circlehide").removeClass("circleshow");
              $("#circle111").addClass("circleshow").removeClass("circlehide");
              $("#circle2").addClass("circlehide").removeClass("circleshow");
              $("#circle222").addClass("circleshow").removeClass("circlehide");
              $("#circle4").addClass("circlehide").removeClass("circleshow");
              $("#circle444").addClass("circleshow").removeClass("circlehide");
              $("#circleLine2").addClass("circle-Line-white").removeClass("circle-Line");
              $("#circleLine3").addClass("circle-Line").removeClass("circle-Line-white");
              $("#circleLine1").addClass("circle-Line-white").removeClass("circle-Line");
            }else  if($(this).index()==3){
              content.$refs.serverTitle.innerHTML='';
              content.$refs.serverInfo.innerHTML='';
              $("#circle4").addClass("circleshow").removeClass("circlehide");
              $("#circle444").addClass("circlehide").removeClass("circleshow");
              $("#circle1").addClass("circlehide").removeClass("circleshow");
              $("#circle111").addClass("circleshow").removeClass("circlehide");
              $("#circle2").addClass("circlehide").removeClass("circleshow");
              $("#circle222").addClass("circleshow").removeClass("circlehide");
              $("#circle3").addClass("circlehide").removeClass("circleshow");
              $("#circle333").addClass("circleshow").removeClass("circlehide");
              $("#circleLine2").addClass("circle-Line-white").removeClass("circle-Line");
              $("#circleLine3").addClass("circle-Line-white").removeClass("circle-Line");
              $("#circleLine1").addClass("circle-Line-white").removeClass("circle-Line");
            }
            $(this).addClass("current");
            $(this).siblings().each(function(k) {
              $(this).removeClass("current");
            })

          })
        })
      },
      handleTouchMove(e) {
          if(this.$refs==null||this.$refs.server_wrapper==null)return;
          var dd = this.$refs.server_wrapper;
          let top=$(dd).offset().top;
          let height=$(dd).height();
          let scrollTop=$(window).scrollTop();
          let count=6;
          if(window.old_scrollTop==null)
          {
            window.old_scrollTop=0;
          }
          if(scrollTop-window.old_scrollTop>0){//
            if(scrollTop-top>-220)
            {
              let currY=parseInt(dd.style.backgroundPositionY);
              if(isNaN(currY))
              {
                currY=0;
              }
              if(currY-count>-220)
              {
                dd.style.backgroundPositionY=(currY-count).toString()+'px';
              }
              else
              {
                dd.style.backgroundPositionY='-220px';
              }
            }
          }else {//
            if(scrollTop<=top+height)
            {

              let currY=parseInt(dd.style.backgroundPositionY);
              if(isNaN(currY))
              {
                currY=0;
              }
              if(currY+count<=0)
              {
                dd.style.backgroundPositionY=(currY+count).toString()+'px';
              }
              else
              {
                dd.style.backgroundPositionY='0px';
              }
            }
          }
          window.old_scrollTop = scrollTop;
      }
    }
  }
</script>



